import './css/main.css';
import './css/util.css';

const mailForm = document.getElementById('mail-form');
const mailInput = mailForm.querySelector('#mail-input');
const passwordInput = mailForm.querySelector('#password-input');
const alertMail = mailForm.querySelector('#error-mail');
const alertPassword = mailForm.querySelector('#error-password');
const showPass = document.getElementById('show-password');


const validMail = ['@gmail.com', '@yahoo.com', '@ukr.net'];

const Mailoptimizer = (extra) => {
    alertMail.classList.add('alert-validate')
    alertMail.setAttribute('data-validate', `${extra}`)
}

const Passwordoptimizer = (extra) => {
    alertPassword.classList.add('alert-validate')
    alertPassword.setAttribute('data-validate', `${extra}`)
}

mailInput.onclick = () => alertMail.classList.remove('alert-validate');
passwordInput.onclick = () => alertPassword.classList.remove('alert-validate');

showPass.onclick = () => passwordInput.type == 'password' ? passwordInput.type = 'text' : passwordInput.type = 'password'

function handleSubmit(event) {
    event.preventDefault()

    const MailVal = mailInput.value
    const PasswordVal = passwordInput.value

    console.log(PasswordVal)

    if (!MailVal.trim()) {
        Mailoptimizer('Input Is Empty!')
    } else if (MailVal.length < 10) {
        Mailoptimizer('Too Short!')
    } else if (!MailVal.includes('@')) {
        Mailoptimizer('Invalid mail structure!')
    } else if (!validMail.map((el) => MailVal.includes(el)).join('').includes('true')) {
        Mailoptimizer('Invalid Mail Inbox!')
    }

    if (!PasswordVal.trim()) {
        Passwordoptimizer('Input Is Empty!')
    } else if (/\s/g.test(PasswordVal)) {
        Passwordoptimizer('Contains Whitespace!')
    } else if (PasswordVal.length < 6 ) {
        Passwordoptimizer('Short Password!')
    } else if (/[A-Z]/g.test(PasswordVal)&&/[a-z]/g.test(PasswordVal)&&/\d/g.test(PasswordVal)) {
        return ''
    } else {
        Passwordoptimizer(`Bad Password!`)
    }
}

mailForm.addEventListener('submit', handleSubmit)

